// 
const providerColors = {
  aliyun: "#00C853" // 
};

// 
const styles = `
  @keyframes breathe {
    0% { r: 3; opacity: 0.8; }
    50% { r: 5; opacity: 0.4; }
    100% { r: 3; opacity: 0.8; }
  }
  .breathe-circle {
    animation: breathe 2s infinite ease-in-out;
  }
`;

const DataCenterMap = () => {
  const [selectedDataCenter, setSelectedDataCenter] = useState(null);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  return (
    <div className="relative h-[1900px] w-full bg-gray-50">
      {/*  */}
      <style>{styles}</style>

      <ComposableMap
        projection="geoMercator"
        projectionConfig={{
          scale: 100, // 
          center: [0, 20], // 
        }}
      >
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map((geo) => (
              <Geography
                key={geo.rsmKey}
                geography={geo}
                fill="#EAEAEC"
                stroke="#D6D6DA"
                strokeWidth={0.5}
              />
            ))
          }
        </Geographies>

        {DataCenters.map((dc, idx) => (
          <Marker key={idx} coordinates={dc.coordinates}>
            <g
              className="cursor-pointer"
              onClick={() => {
                setSelectedDataCenter(dc);
                setSidebarOpen(true);
              }}
            >
              {/*  */}
              <circle
                cx={0}
                cy={0}
                r={3}
                fill={providerColors.aliyun}
                className="breathe-circle"
              />
            </g>
          </Marker>
        ))}
      </ComposableMap>

      {/*  */}
      <div 
        className={`absolute top-0 right-0 h-full bg-white shadow-lg transition-transform duration-300 ${
          sidebarOpen ? 'translate-x-0' : 'translate-x-full'
        }`}
        style={{ width: '300px' }}
      >
        <button
          onClick={() => setSidebarOpen(!sidebarOpen)}
          className="absolute -left-12 top-4 p-2 bg-white rounded-l-lg shadow-md"
        >
          <ChevronRightIcon className="h-6 w-6 text-gray-600 transform transition-transform" />
        </button>

        {selectedDataCenter && (
          console.log(selectedDataCenter),
          <div className="p-6">
            <h3 className="text-xl font-semibold mb-4">
              {selectedDataCenter.provider}-{selectedDataCenter.name}
            </h3>
            <div className="flex items-center mb-2">
              <span 
                className="w-3 h-3 rounded-full mr-2"
                style={{ backgroundColor: providerColors.aliyun }}
              />
              <span className="capitalize">{selectedDataCenter.region}</span>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default DataCenterMap;